[data-pulse="interested"] {
  width: 250px;
  top: -85px;
  left: -7px;
}
.icon-pulse {
  transform: translateX(-50%) translateY(-50%);
  width: 15px;
  height: 15px;
}
.icon-pulse.icon--pulse-stop:before {
  display: none;
}
.icon-pulse.icon--pulse-stop:after {
  animation: none;
}
.icon-pulse:before {
  content: '';
  position: relative;
  display: block;
  width: 300%;
  height: 300%;
  box-sizing: border-box;
  margin-left: -100%;
  margin-top: -100%;
  border-radius: 45px;
  background-color: rgba(80, 116, 233, 0.5);
  animation: pulse-ring 1.25s cubic-bezier(0.215, 0.61, 0.355, 1) infinite;
}
.icon-pulse:after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  display: block;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  border-radius: 15px;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.3);
  animation: pulse-dot 1.25s cubic-bezier(0.455, 0.03, 0.515, 0.955) -0.4s infinite;
}
.icon-pulse:hover:after {
  background-color: #5074E9;
  cursor: pointer;
}
@keyframes pulse-ring {
  0% {
    transform: scale(0.33);
  }
  80%,
  100% {
    opacity: 0;
  }
}
@keyframes pulse-dot {
  0% {
    transform: scale(0.8);
  }
  50% {
    transform: scale(1);
  }
  100% {
    transform: scale(0.8);
  }
}



